<template>
    <div>
        <div class="auth-box-w">
            <div class="logo-w">
                <router-link to="/">
                    <img alt="" :src="$store.state.CONSTANTS.configs.LOGO_PATH" class="img-fluid">
                </router-link>
            </div>
            <h4 class="auth-header">
                Entrar
            </h4>
            <form @submit.prevent="login">
                <div class="form-group">
                    <label for="">Email</label>
                    <input class="form-control" placeholder="Seu email" type="email" v-model="email" required/>
                    <div class="pre-icon os-icon os-icon-user-male-circle"></div>
                </div>
                <div class="form-group">
                    <label for="">Senha</label>
                    <input class="form-control" placeholder="Sua senha" type="password" v-model="password" required/>
                    <div class="pre-icon os-icon os-icon-fingerprint"></div>
                </div>
                <div class="buttons-w">
                    <button class="btn btn-primary">Entrar</button>
                    <div class="form-check-inline">
                        <label class="form-check-label">
                            <input class="form-check-input" type="checkbox" v-model="stay_logged"/>Mantenha-me logado
                        </label>
                    </div>
                    <p class="text-center">
                        <router-link :to="{name:'register'}">Não tenho cadastro</router-link>
                    </p>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                email: 'enel@contato.com.br',
                password: '123456',
                stay_logged: false,
            }
        },
        methods: {
            login() {
                this.preload(true);

                this.$store.dispatch("login", {
                    email: this.email,
                    password: this.password,
                    stay_logged: this.stay_logged
                })
                    .then(response => {
                        this.$router.push('admin');
                        setTimeout(() => {
                            this.$snotify.success('Seja bem vindo!');
                        }, 500);
                    })
                    .catch(err => {
                        if (err.response.status === 401 && err.response.data.message) {
                            this.$snotify.warning('Verifique seu email');
                        } else if (err.response.status === 500) {
                            this.$snotify.error('Ops, parece que algo deu errado ;(');
                        } else {
                            this.$snotify.error('Erro de autenticação');
                        }
                    }).finally(() => this.preload(false));

            }
        },
        beforeCreate() {
            let lastRoute = localStorage.getItem('lr');
            let token = localStorage.getItem('access_token');
            if (token) {
                let id = localStorage.getItem('id');
                let route = id ? {name: lastRoute,params:{id:id}} : {name: lastRoute};
                this.$router.push(route)
            }
        }
    }
</script>
<style scoped>

    .all-wrapper.menu-side.with-pattern {
        margin-top: 6%;
    }

    @media screen and (max-width: 760px) {
        .all-wrapper.menu-side.with-pattern {
            margin-top: 15%;
        }
    }

</style>